'use client'
import axios from 'axios'
import Link from 'next/link'
import Header from '../components/Header'
import Image from 'next/image'
import { motion } from 'framer-motion'

async function getProducts() {
  const res = await axios.get('https://sublime-beauty-ac64b64f48.strapiapp.com/api/products?populate=*')
  return res.data.data
}

export default async function HomePage() {
  const products = await getProducts()

  return (
    <div className="relative w-full min-h-screen overflow-hidden">
      {/* üî¥ Background Video */}
      <video
        className="absolute inset-0 w-full h-full object-cover"
        autoPlay
        loop
        muted
        playsInline
      >
        <source src="/home-page-video.mp4" type="video/mp4" />
      </video>

      {/* üî≤ Dark Overlay */}
      <div className="inset-0 bg-black/40 z-10" />

      {/* üåê Content Overlay */}
      <div className="relative z-20">
        <Header />
        <div className="pl-4 lg:pl-10 pt-28">
          <ul className="flex gap-6 font-semibold text-sm uppercase text-white">
            <li><Link href="/collections/men-all-shoes">Men</Link></li>
            <li><Link href="/collections/women-all-shoes">Women</Link></li>
            <li><Link href="/collections/kids-all-shoes">Kids</Link></li>
            <li><Link href="/collections/summer-2025">New In</Link></li>
            <li><Link href="/collections/sale">Sale</Link></li>
          </ul>
        </div>
      </div>

      {/* üì∑ Animated Image Grid (Appears after video section) */}
      <div className="relative z-20 bg-white py-16">
        <div className="grid grid-cols-2 md:grid-cols-3 gap-4 px-4">
          {[1, 2, 3].map((num) => (
            <motion.div
              key={num}
              initial={{ y: 80, opacity: 0 }}
              whileInView={{ y: 0, opacity: 1 }}
              transition={{ duration: 0.5, delay: num * 0.1 }}
              viewport={{ once: true }}
              className="relative w-full h-48 md:h-64"
            >
              <Image
                src={`/Home-page-img${num}.webp`}
                alt={`Product ${num}`}
                fill
                className="object-cover rounded-xl shadow-md"
              />
            </motion.div>
          ))}
        </div>
      </div>
    </div>
  )
}
